@font-face {
  font-family: "Anurati";
  font-style: normal;
  font-weight: 400;
  src: local("Anurati"), url("https://fonts.cdnfonts.com/s/14024/Anurati-Regular.woff") format("woff");
}
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  overflow-x: unset;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  user-select: none;
  color: #000000;
}

.contenedor {
  width: 98%;
  max-width: 140rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: #000000;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

