.header {
  background-image: url("/build/img/headerImage.webp");
  height: auto;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 0;
  padding-top: 5.5rem;
  padding-bottom: 5.5rem;
}

.shadow {
  height: auto;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-bottom: 5.5rem;
}

.sign {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 10;
  color: #ffffff;
}
.sign img {
  padding-right: 2rem;
  width: 8rem;
}
@media (min-width: 768px) {
  .sign img {
    width: 10rem;
  }
}
.sign p {
  font-weight: bold;
  font-size: 3.4rem;
}
@media (min-width: 768px) {
  .sign p {
    font-size: 4.2rem;
  }
}

.title {
  position: relative;
  z-index: 10;
  color: #ffffff;
}

.principal {
  text-align: start;
  width: 38rem;
  font-size: 5rem;
  font-weight: bold;
  line-height: 1.2;
  position: relative;
  padding-bottom: 5rem;
  color: #ffffff;
}
.principal::before {
  content: "";
  position: absolute;
  width: 2.4rem;
  height: 0.4rem;
  background-color: currentColor;
  bottom: 0;
  left: 0;
}
@media (min-width: 768px) {
  .principal {
    font-size: 8rem;
    width: 70rem;
  }
}

.presentation {
  width: 35rem;
  font-size: 3.2rem;
  font-weight: 500;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .presentation {
    width: 70rem;
  }
}

.integration {
  height: auto;
  background-color: #BCA6D7;
  padding-top: 6rem;
  padding-bottom: 6rem;
}

.all {
  text-align: center;
  font-weight: bold;
  font-size: 3.2rem;
  line-height: 1.2;
  width: 35rem;
}
@media (min-width: 768px) {
  .all {
    width: 65rem;
  }
}
@media (min-width: 1024px) {
  .all {
    width: 100rem;
  }
}

.cards {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .cards {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 5rem;
  }
}
@media (min-width: 1024px) {
  .cards {
    row-gap: 0rem;
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: #ffffff;
  padding: 0;
  width: 30.1rem;
  border-radius: 2rem;
  margin: 0 auto;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .card {
    margin: 0 auto;
  }
}

.infoCard {
  background-color: #F7F7F7;
  padding: 1rem 3rem;
  border-radius: 0 0 2rem 2rem;
  height: 70%;
}

.titleCard {
  font-size: 2.4rem;
  margin-top: 1rem;
}

.descriptionCard {
  font-size: 2.4rem;
  line-height: 1.2;
  margin-top: 0.5rem;
}

.logotipe {
  display: flex;
  align-items: center;
  margin: 1rem 3rem;
}
.logotipe img {
  width: 5rem;
  height: 5rem;
  margin-right: 1.5rem;
}
.logotipe p {
  font-size: 2.4rem;
  color: currentColor;
}

.boton {
  display: flex;
  justify-content: center;
  margin-top: 10rem;
}

.buttom {
  display: inline-block;
  background-color: #0675D1;
  padding: 1.4rem 5rem;
  border-radius: 6rem;
  color: #ffffff;
  font-size: 2.4rem;
  font-weight: bold;
  text-align: center;
}
.buttom:hover {
  background-color: #0567b8;
  transition: background-color 0.3s linear;
}
@media (min-width: 768px) {
  .buttom {
    padding: 2rem 8rem;
    font-size: 3rem;
  }
}

.information {
  padding-top: 10rem;
  position: relative;
  padding-bottom: 20rem;
}
.information::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 0.3rem;
  position: absolute;
  bottom: 0;
  left: 0rem;
  right: 0rem;
}
@media (min-width: 1024px) {
  .information::before {
    bottom: 0;
    left: 27rem;
    right: 0;
    width: 60%;
  }
}

.item, .itemSec {
  margin: 0 auto;
  margin-top: 8rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .item, .itemSec {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
.item .infoItem, .itemSec .infoItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .item .infoItem, .itemSec .infoItem {
    align-items: flex-end;
  }
}
.item .titleItem, .itemSec .titleItem {
  color: #5347C0;
  font-size: 3.3rem;
  line-height: 1;
  font-weight: bold;
  margin-bottom: 0;
  width: 35rem;
}
@media (min-width: 768px) {
  .item .titleItem, .itemSec .titleItem {
    width: 70rem;
  }
}
@media (min-width: 1024px) {
  .item .titleItem, .itemSec .titleItem {
    width: 45rem;
  }
}
.item .descItem, .itemSec .descItem {
  font-size: 2.2rem;
  line-height: 1;
  font-weight: bold;
  margin-bottom: 0;
  width: 35rem;
}
@media (min-width: 768px) {
  .item .descItem, .itemSec .descItem {
    width: 70rem;
  }
}
@media (min-width: 1024px) {
  .item .descItem, .itemSec .descItem {
    width: 45rem;
  }
}
.item .presentationItem, .itemSec .presentationItem {
  line-height: 1.5;
  color: #000000;
  width: 35rem;
}
@media (min-width: 768px) {
  .item .presentationItem, .itemSec .presentationItem {
    width: 70rem;
  }
}
@media (min-width: 1024px) {
  .item .presentationItem, .itemSec .presentationItem {
    color: rgba(0, 0, 0, 0.6);
    width: 45rem;
  }
}
.item .imageItem, .itemSec .imageItem {
  position: relative;
  width: 50%;
  margin: 0 auto;
}
.item .imageItem img, .itemSec .imageItem img {
  width: 20rem;
  max-width: 20rem;
  position: absolute;
  z-index: 5;
  opacity: 0;
}
@media (min-width: 768px) {
  .item .imageItem img, .itemSec .imageItem img {
    right: 20rem;
    top: -25rem;
    right: 20rem;
    opacity: 0.4;
  }
}
@media (min-width: 1024px) {
  .item .imageItem img, .itemSec .imageItem img {
    width: 35rem;
    opacity: 1;
    top: 3rem;
    left: -1rem;
    max-width: 35rem;
  }
}
.item .imgShadow, .itemSec .imgShadow {
  width: 30rem;
  height: 30rem;
  position: absolute;
  bottom: -5rem;
  border-radius: 80%;
  background: linear-gradient(180deg, rgba(188, 166, 215, 0.2) 50%, rgba(83, 71, 192, 0.2) 100%);
  left: -10rem;
}
@media (min-width: 768px) {
  .item .imgShadow, .itemSec .imgShadow {
    left: -15rem;
  }
}
@media (min-width: 1024px) {
  .item .imgShadow, .itemSec .imgShadow {
    width: 40rem;
    height: 40rem;
    background: linear-gradient(180deg, #bca6d7 50%, #5347c0 100%);
    left: -5rem;
    bottom: -4rem;
    right: 0;
  }
}

.itemSec .infoItem {
  align-items: center;
}
@media (min-width: 1024px) {
  .itemSec .infoItem {
    align-items: flex-start;
  }
}
.itemSec .imgShadow {
  left: -8rem;
  bottom: -45rem;
}
@media (min-width: 768px) {
  .itemSec .imgShadow {
    left: 20rem;
  }
}
@media (min-width: 1024px) {
  .itemSec .imgShadow {
    left: 3rem;
    bottom: -4rem;
    right: 0;
  }
}
.itemSec .imageItem img {
  position: absolute;
  width: 30rem;
  top: 15rem;
  left: 0;
}
@media (min-width: 768px) {
  .itemSec .imageItem img {
    left: 15rem;
  }
}
@media (min-width: 1024px) {
  .itemSec .imageItem img {
    width: 46rem;
    top: 0;
    left: 7rem;
  }
}

.ciber {
  font-size: 1.2rem;
  position: absolute;
  bottom: -5rem;
}

.asesorInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.asesorInfo p {
  width: 35rem;
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
  line-height: 1.2;
  margin: 8rem 0;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .asesorInfo p {
    width: 60rem;
  }
}

.testimonial {
  background-color: #CFA5F9;
  margin-top: 14rem;
}
.testimonial blockquote {
  text-align: center;
  margin: 0;
  padding: 1rem;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3.2rem;
  font-family: Anurati, sans-serif;
}

.footer {
  background-color: #1B1B1B;
  padding: 5rem 3rem;
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .footer {
    padding: 8rem;
  }
}
@media (min-width: 1024px) {
  .footer {
    padding: 15rem;
    padding-bottom: 0;
  }
}

.infoFooter {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  color: #ffffff;
}
@media (min-width: 768px) {
  .infoFooter {
    gap: 1rem;
  }
}

.leftFooter {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
.leftFooter img {
  width: 8rem;
}
.leftFooter .titleAdobe {
  color: #ffffff;
  font-weight: bold;
  font-size: 2.4rem;
  margin: 0;
}

.contacto {
  margin-top: 1rem;
}

.contactoTitle {
  font-size: 1.8rem;
  font-weight: lighter;
}
@media (min-width: 768px) {
  .contactoTitle {
    font-size: 2.4rem;
  }
}

.info {
  margin: 0;
  line-height: 1.8;
  font-size: 1.6rem;
}
.info img {
  width: 1.4rem;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .info img {
    width: 1.8rem;
  }
}
@media (min-width: 768px) {
  .info {
    font-size: 2rem;
  }
}

.rightFooter {
  margin: 0 auto;
  margin-top: 5rem;
  text-align: end;
}
@media (min-width: 1024px) {
  .rightFooter {
    text-align: start;
  }
}

.copy {
  color: #ffffff;
  position: relative;
  margin-top: 5rem;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.copy::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.2rem;
  background-color: #ffffff;
  top: 0;
  left: 0;
}
@media (min-width: 1024px) {
  .copy {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
  }
}

.social {
  display: flex;
}
.social li {
  margin-left: 2rem;
}
.social li a {
  color: #ffffff;
}

.textCopy {
  display: flex;
  font-size: 1.6rem;
  margin: 1rem;
  margin-right: 0;
}
.textCopy span {
  color: red;
}
@media (min-width: 1024px) {
  .textCopy {
    margin: 1.6rem;
  }
}